.LoginCont{
  background: #fff;
  min-height: 70vh;
  padding: 100px 15px 50px 15px;;
}
.LoginForm{
  width: 560px;
  margin: 0px auto ;  
  padding: 50px 0 ;
  min-height: 400px;
  vertical-align: middle;
  box-shadow: 0 1px 3px 0 rgba(41,70,97,.2);
  position: relative;
  align-items: center;
  padding: 25px 20px 45px 20px;
  border-radius:  2px; 
  box-align: start;
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;
}

.LoginFormSides{
  flex: 1 1;
  display: flex;
  max-width: 270px;
  min-width: 220px;
  margin: 0px auto;

}

@media only screen and (max-width: 680px){
  .LoginForm{
    width: 90%;    
  }
}


.ActionContainer{
  margin: 50px auto 0 auto;
  width: 162px;
}

.LoginFormContainer{
  max-width: 250px;
  display: block;
  position: relative;
}


.LoginFormItem{
  display: block
}

.InputLoginClass{    
  min-width: 229px;
  padding: 13px;
  border-radius: 13px;  
  outline: none;
  color: #546b81;
  border: 1px solid #0091ea;
}

.InputLoginClassError{    
  min-width: 229px;
  padding: 13px;
  border-radius: 13px;  
  outline: none;
  color: #546b81;
  border: 1px solid firebrick;
}

 .errMsg{
  color:firebrick;
  font-size: 13px;
  padding: 5px 9px; 
}

.InputLabelClass{
  margin: 15px 10px;
}

.LoginFormButton{
  min-width: 160px;    
  height: 44px;
  vertical-align: middle;
  box-shadow: 0 1px 3px 0 rgba(41,70,97,.2);
  background-color: #0091ea;
  color: #fff;
  border: 1px solid #0091ea;
  padding: 13px;
  border-radius: 13px;  
  outline: none;
  cursor: pointer;
}

.LoginDisabledFormButton{
  min-width: 160px;    
  height: 44px;
  vertical-align: middle;
  box-shadow: 0 1px 3px 0 rgba(41,70,97,.2);
  background-color: #aaa;
  color: #999;
  border: 1px solid #aaa;
  padding: 13px;
  border-radius: 13px;  
  outline: none;
  cursor: default;
}


.InfoFormSides{
  text-align: center;
  padding: 10% 20%; 
}

.InfoFormSides .title{
  font-family: Colfax-Regular,helvetica,sans-serif;
  font-style: normal;
  font-size: 21px;
  font-weight: 600;
  color: inherit;
  text-rendering: optimizeLegibility;
  margin: 15px 0; 
}
.InfoFormSides .description{
  font-family: Colfax-Regular,helvetica,sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  color: #aaa;
  text-rendering: optimizeLegibility;
  margin: 25px 0 0 0;
}



