    font-size: 2.36vw;
    color: #fff;
    transition: color 1s cubic-bezier(.165,.84,.44,1);
    text-shadow: 2px 2px 4px rgba(0,0,0,.45);
    margin: 1vw 0;
   
    align-items: center;
    font-weight: 700;
}

.heroHRM-title {
    
    min-height: 13.2vw;
    position: relative;
    margin-bottom: 1.2vw;
    margin: 1vw 3vw;
    transform-origin: left bottom;
    transform: scale(1) translate3d(0px, 0px, 0px);
    transition-duration: 100ms;   
    /*transition-delay: 400ms;*/
    top: 5vw;    
}

.player-view{
    --tr-time-player-view:0.75s;
    transition: transform var(--tr-time-player-view) cubic-bezier(.165,.84,.44,1);
}

.textTransfrom {
    --tr-time-text-view:0.5s;
    transition: transform var(--tr-time-text-view) cubic-bezier(.165,.84,.44,1);
}



